.Home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-box {
    width: fit-content;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.bio {
    font-size: 25px;
    font-family: Titillium Web;
    margin-top: -1.2cm;
    align-items: center;
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    height: fit-content;

    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0;
    /* Add this line to remove padding */


}


@import url('https://fonts.googleapis.com/css?family=Bangers&display=swap');



.names {

    z-index: 2;
    font-size: 10em;
    letter-spacing: 15px;
    text-transform: uppercase;
    text-shadow: 1px 1px hsl(249, 82%, 67%),
        2px 2px hsl(249, 50%, 40%),
        3px 3px hsl(249, 50%, 35%),
        4px 4px hsl(249, 50%, 34%),
        5px 5px hsl(249, 50%, 33%),
        6px 6px hsl(249, 50%, 32%),
        7px 7px hsl(249, 50%, 31%),
        8px 8px hsl(249, 50%, 30%),
        9px 9px hsl(249, 50%, 29%),
        10px 10px hsl(249, 50%, 28%);

    color: hsl(261, 98%, 67%);
    display: flex;
    overflow: hidden;
    font-family: 'Bangers', cursive;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;

    &:before {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: rotate(0) translateX(-50%) translateY(-50%);
        transform-origin: 0 0;
        width: 200%;
        height: 200%;

        content: '';
        // animation: rotation 20s infinite linear;
    }
}